<template>
    <div>
        <p>With desc</p>
        <u-button @on-click="info(false)">Info</u-button>
        <u-button @on-click="success(false)">Success</u-button>
        <u-button @on-click="warning(false)">Warning</u-button>
        <u-button @on-click="error(false)">Error</u-button>
        <p>Only title</p>
        <u-button @on-click="info(true)">Info</u-button>
        <u-button @on-click="success(true)">Success</u-button>
        <u-button @on-click="warning(true)">Warning</u-button>
        <u-button @on-click="error(true)">Error</u-button>
    </div>
</template>
<script>
    export default {
        methods: {
            info (nodesc) {
                this.$Notice.info({
                    title: 'Notification title',
                    desc: nodesc ? '' : 'Here is the notification description. Here is the notification description. '
                });
            },
            success (nodesc) {
                this.$Notice.success({
                    title: 'Notification title',
                    desc: nodesc ? '' : 'Here is the notification description. Here is the notification description. '
                });
            },
            warning (nodesc) {
                this.$Notice.warning({
                    title: 'Notification title',
                    desc: nodesc ? '' : 'Here is the notification description. Here is the notification description. '
                });
            },
            error (nodesc) {
                this.$Notice.error({
                    title: 'Notification title',
                    desc: nodesc ? '' : 'Here is the notification description. Here is the notification description. '
                });
            }
        }
    };
</script>



